	module.exports = {"data":{"post":{"html":"<p>(Pull the pants off the Bavarians!) Bayern Munich is the New York Yankees of German soccer: rich, arrogant, hated and successful. It is therefore gives me great satisfaction to report that <a title=\"Werder demütigt den FC Bayern ... [jimmiz journal]\" href=\"http://jimmiz.blogg.de/eintrag.php?id=374\">Bayern was defeated by Bremen at home 3-1</a>, thus clinching the Bundesliga title for Bremen. I may not live to see Bayern relegated, but it's nice to see them lose once in a while, especially to a northern team.</p>","fields":{"title":"Zieht den Bayern die Lederhosen aus!","date":"08 May 2004","url":"https://papascott.de/2004-05-08-zieht-den-bayern-die-lederhosen-aus/"}}},"pathContext":{"slug":"/2004-05-08-zieht-den-bayern-die-lederhosen-aus/","prev":{"fields":{"title":"Trial Marriage","date":"08 May 2004","slug":"/2004-05-08-trial-marriage/"}},"next":{"fields":{"title":"Brad Choate: OS X software inventory","date":"09 May 2004","slug":"/2004-05-09-brad-choate-os-x-software-inventory/"}}}}

